.weather-container{
    height: 575px;
    background-color: #6d53d7;
    width: 60%;
    justify-content: center;
}

.weather-header {
    align-items: center;
    display: flex;
    flex-direction : column;
    width: 100%;
    
}

.weather-header div {
    display: flex;
    border-bottom :2px solid #eee;
    width: 80%;
}
button {
    color: white;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
}
.weather-header h3 {
    color: #e1dfdd;
    letter-spacing: 0.2rem;
    font-size: 1.4rem;
}
.search-input-box{
    background: transparent;
    border: none;
    outline: none;
    color: white; 
    width: 100%;
    font-size: 1.2rem;
}


.search-input-box::-webkit-input-placeholder{
    color: #b7b5be;
    font-size: 1.2rem;

}

.icon,.btn {
    background: transparent;
    border: none;
    outline: none;
}

.icon {
    color:#e1dfdd;
}

.Error {
    color :#8f0019;
    
    align-items: center;
    justify-content: center;
    display: flex;
}